export default class ListView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  componentWillMount() {
    starwarsService()
      .then(res => {
        this.setState({ data: res.data.getPersons });
      })
      .catch(err => {
        console.log(err);
      });
  }

  render() {
    return (
      <section className="list-view" style={{backgroundColor: '#151515'}}>
        <ul className="ui-list">
          {Array.isArray(this.state.data) && this.state.data.map(person => (
            <li className="ui-item">
              <h3 className="ui-item__title">{person.name}</h3>
              <h3 className="ui-item__title">{person.name}</h3>
            </li>
          ))}
        </ul>
      </section>
    );
  }
}
